body {
    background: #31343b;
    color: #eee;
    font-family: monospace;
}

.top {
    padding-top: 40px;
}

svg {
    position: absolute;
}

.mouse-tile {
    fill: none;
    pointer-events: all;
}

.mouse-tile:hover {
    fill: rgba(255, 255, 255, 0.2);
    cursor: pointer;
}

.attack {
    /* width: 80px; */
    height: 60px;
    border: #384967 solid 1px;
    text-align: center;
    vertical-align: middle;
    line-height: 60px;
    cursor: pointer;
}

.attack.selected-attack {
    border: #386767 solid 3px;
}

.attack.selected-attack:hover {
    border: #386767 solid 3px;
}
.attack:hover {
    background-color: rgba(255, 255, 255, 0.1);
    border-color: #384967;
}

.menu-button {
    margin-right: auto;
    margin-left: auto;
    background-color: rgba(71, 41, 92, 0.75);
    color: #ddd;
    text-align: center;
    width: 300px;
    font-size: 32px;
}

.menu-container {
    margin-left: auto;
    margin-right: auto;
    width: 400px;
    background-color: rgba(71, 41, 92, 0.75);
    border-radius: 5px;
    border: 2px solid #384967;
}

.menu-level {
    display: inline-block;
    margin: 10px;
    width: 60px;
    height: 60px;
    padding: 9px;
    /* background-color: rgba(71, 41, 92, 1); */
    /* background-color: #ddd; */
    background-image: url("/src/assets/chip_level.png");
}
.menu-level-inside {
    width: 42px;
    height: 42px;
    line-height: 42px;
    text-align: center;
}

.menu-level-inside:hover {
    background-color: rgba(255, 255, 255, 0.1);
    cursor: pointer;
}

.screen-wrapper {
    width: 1280px;
    height: 720px;
    background-image: url("/src/assets/menu_bg.png");
}

.semi-cover-bg {
    border: 2px solid #eee;
    background-color: rgba(57, 38, 71, 0.5);
}

.board-wrapper {
    width: 720px;
    height: 490px;
    padding: 15px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
}

.infobar {
    padding: 15px
}

.game-wrapper {
    margin: 50px;
    width: 1280px;
    height: 720px;
    margin-right: auto;
    margin-left: auto;
}
